.npsCardBox {
    width: 100%;
    overflow: hidden;
  }

  .npsPeopleBox {
    width: 80px;
  }

  .npsPeople {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  .npsPeopleNumber {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    color: white
  }

  .npsPeopleDescBox {
    display: flex;
    flex-direction: column;
  }

  .npsPeopleAllCnt {
    display: flex;
    justify-content: center;
    font-size: 20px;
  }

  .npsPeoplePercent {
    display: flex;
    justify-content: center;
    font-size: 14px;
  }

  .npsPeopleGroupBox {
    margin-top: 20px;
    display: flex;
    justify-Content: space-around;
    height: 180px;
  }


  .tolpa{
    display: flex;
    justify-content: center;
    height: 93px;
    width: 93px;
  }


.toltopaDesc {
    display: flex;
    flex-direction: column;
}

.npsScoreBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.npsScore {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}
.npsScorePercent {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  margin-left: 10px;
  font-weight: 400;
}

.tolpaDescItem {
    display: flex;
    justify-content: center;
}

