.circle {
    height: 300px;
}

.arrow {
  width: 31px;
  height: 125px;
  position: absolute;
  left: 45%;
  bottom: 130px;
  transform-origin: center 111px;
  transition: 0.3s;
}

.cercleBox {
    display: flex;
    flex-direction: column;
    position: relative;
}



.intervalReportTitle{
  display: flex;
    justify-content: space-between;
}

.itervalReprotBox {
  padding: 25px 0px 25px 0px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0,0,0,.1);
  background: #fff;
  display: flex;
  flex-direction: column;
  width: 1775px;
  overflow: auto;
}

.title {
  display: flex;
  border-bottom: 1px solid;
    border-bottom-color: lightgray;
}

.titleCell{
  padding: 20px 10px 20px 10px;
  display: flex;
  justify-content: center;
}

.bodyCell{
  width: 100px;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.historyDateDesc{
  width: 167px;
  padding: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.bodyCellBig{
  width: 167px;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.redCell{
  margin-left: 3px;
}

.intervalReport_bodyBox{
  display: flex;
  /* margin-left: 126px; */
  padding-top: 10px;
}

.dataCell {
  display: flex;
}

.intervalReprot_title_avgTime{
  min-width: 240px;
}

.intervalReprot_title_deference{
  min-width: 255px;
}

.asd {
  width: 126px;
}

.exportData{
  width: 100%;
  display: flex;
}

