.boardHeaderBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  background-size: cover!important;
}

.boardHeaderLogo {
  padding: 0;
}

.boardHeaderLogo img {
  width: auto;
  height: inherit;
  object-fit: contain;
  align-self: flex-start;
  display: flex;
}

.boardHeaderTextTimerBox {
  display: flex;
  justify-content: center;
}

.boardHeaderText {
  display: flex;
  align-items: center;
  white-space: pre-line;
}
