.boardColumnsBox {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  flex: 1;
}

.boardColumnsTitlesBox {
  display: flex;
  height: 14%;
  flex: 1
}

.boardColumnsTitlesBox div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  padding: 0px 0;
}

.boardColumnsCellBox {
  display: flex;
  flex: 1;
}

.boardColumnCell {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  text-align: center;
}
