.btnAdd {
  cursor: pointer;
    padding: 10px 20px;
    background: #ccc;
    box-shadow: none;
    border-radius: 3px;
    border: 0;
    margin: 5px 5px 25px;
    color: #fff;
    -webkit-transition: 0.1s ease-out;
    transition: 0.1s ease-out;
}

.layoutSettingBox {
  display: flex;
  flex-direction: column;
}
