@import './board/boardPreview/common.scss';
@import './board/boardPreview/columns.scss';
@import './board/boardPreview/big.scss';
@import './board/boardPreview/videoNewsScheduleBox.scss';
@import './board/boardPreview/header.scss';
@import './board/boardPreview/carousel.scss';
@import './multiSelect.scss';
@import './npsCard.scss';
@import './board/boardEditor/index.scss';
@import './Tooltip.scss';
