.boardVideoNewsScheduleBox {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.boardNewScheduleBox {
  display: flex;
  flex: 1;
}

.boardNewsBox {
  flex: 1;
  display: flex;
  flex-direction: column;
  border: 1px solid #a19fa0;
}

.boardTimerScheduleBox {
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid #a19fa0;
}

.boardVideoBox {
  font-size: 66px;
  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  overflow: hidden;
}

.boardVideoBox video {
  max-width: 100%;
}

#video-volume {
  z-index: 10;
  width: 100%;
  outline: none;
  
  &:focus {
    outline: none;
  }
}

.carousel {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden!important;
}

.carousel ul {
  flex: 1;
  height: 100%;
}

.carousel ul div {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 20px 0 0;
}

.carousel ul div p:nth-of-type(1) {
  display: flex;
  align-items: flex-start;
  padding: 0px 10px 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.boardTimerBox {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 10px;
}

.boardScheduleBox {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 10px;

}

.boardScheduleBox  p {
  white-space: pre-line;
}

.boardScheduleBorder {
  position: absolute;
  top: 0;
  width: 80%;
  height: 1px;
  background: #000;
}
