body {
  margin: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.boardGlobalBox {
  height: 100vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  margin: 0 auto;
}

.boardGlobalBox * {
  font-weight: normal;
}

.boardBodyBox {
  flex: 1;
  display: flex;
  align-items: stretch;
}
.boardHeaderTextBox {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: fit-content;
  .footerString {
    display: flex;
  }
}
.boardFooterBox {
  display: flex;
  align-items: center;
  height: 5.5%;
}
.footerString {
  display: flex;
  align-items: center;
  .boardTimerBox {
    display: inline-flex;
    flex-direction: row-reverse;
    p {
      margin: 0;
    }
  }
}
